@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
body{
  background: black url("/images/background.jpg")no-repeat center center fixed;
  background-size: cover;
  color: rgb(7, 7, 7)
}

.title{
  font-family: 'Dancing Script', cursive;
  font-size: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  color:  rgb(42, 101, 156);

}

.heading{
  color:  rgb(42, 101, 156);
  font-family: 'Dancing Script', cursive;
}

.profile-paragraph{
  margin-top: 20px;
}
.resume{
  margin-top: 20px;
  font-size: 15px;
  color:  rgb(42, 101, 156);
}
.fab{
  color: rgb(252, 250, 250)
}

.fas{
  color:  rgb(42, 101, 156);
}

.link{
  color: black;
}


p{
  color: black;
}

.avatar-flip {
  border-radius: 100px;
  overflow: hidden;
  height: 200px;
  width: 200px;
  position: relative;
  margin: auto;
  transform: rotate(-25deg);
  float: left;
  display: flex;
  justify-content: center;
  align-items: center;

}

a{
  color: rgb(255, 255, 255);
}

.icon{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  border-radius: 100%;
}

.reacticon{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  border-radius: 100%;
}

.mongooseicon{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  border-radius: 50%;
}

